// src/components/details/SymptomDetailCard.jsx

import React from 'react';
//  Ant Design  Typography ()  Card ()
import { Typography, Card } from 'antd';
//  renderNaturalPrescriptions 
import { renderNaturalPrescriptions } from '../../utils/helpers'; // 

//  Typography  Title  Paragraph 
const { Title, Paragraph } = Typography;

/**
 * @function SymptomDetailCard
 * @description 
 * 
 * 
 * @param {Object} props - 
 * @param {Object} props.symptomData - 
 * @param {Object} props.database -  renderNaturalPrescriptions 
 */
function SymptomDetailCard({ symptomData, database }) {
  //  symptomData 
  //  undefined  .map()
  const { 
    name, // name  undefined
    possibleCauses = [], 
    homeCare = [], 
    whenToSeeADoctor = [], 
    naturalPrescriptions = [] 
  } = symptomData || {}; //  symptomData  undefined  null

  //  symptomData  name 
  if (!symptomData || !name) {
    return (
      <Card className="result-card">
        <Paragraph style={{ textAlign: 'center', color: 'red' }}>
          
        </Paragraph>
      </Card>
    );
  }

  return (
    //  ResultsView.css  .result-card 
    <Card className="result-card">
      {/*  name  */}
      <Title level={2}>&nbsp;{name}</Title>
      
      {/*  possibleCauses  */}
      {possibleCauses.length > 0 && (
        <>
          <Title level={4}></Title>
          {/*  */}
          <ul>{possibleCauses.map(cause => <li key={cause}>{cause}</li>)}</ul>
        </>
      )}
      
      {/*  homeCare  */}
      {homeCare.length > 0 && (
        <>
          <Title level={4}></Title>
          {/*  */}
          <ul>{homeCare.map(care => <li key={care}>{care}</li>)}</ul>
        </>
      )}
      
      {/*  whenToSeeADoctor  */}
      {whenToSeeADoctor.length > 0 && (
        <>
          <Title level={4}></Title>
          {/*  */}
          <ul>{whenToSeeADoctor.map(when => <li key={when}>{when}</li>)}</ul>
        </>
      )}
      
      {/*  naturalPrescriptions  */}
      {/*  */}
      {naturalPrescriptions.length > 0 && renderNaturalPrescriptions(naturalPrescriptions, database)}
      
      {/*  */}
      <Paragraph style={{ fontSize: '0.8em', color: '#777', marginTop: '1em' }}>
        
      </Paragraph>
    </Card>
  );
}

export default SymptomDetailCard;