// src/components/details/NaturalPrescriptionDetailCard.jsx

import React from 'react';
//  Ant Design  Typography ()  Card ()
import { Typography, Card } from 'antd'; //  Card 

//  Typography  Title  Paragraph 
const { Title, Paragraph } = Typography;

/**
 * @function NaturalPrescriptionDetailCard
 * @description 
 * 
 * 
 *  database prop prescriptionData 
 * @param {Object} props - 
 * @param {Object} props.prescriptionData - 
 */
function NaturalPrescriptionDetailCard({ prescriptionData }) {
  return (
    //  ResultsView.css  .result-card 
    <Card className="result-card">
      {/*  */}
      <Title level={2}>{prescriptionData.icon || ''}&nbsp;{prescriptionData.name}</Title>
      
      {/*  */}
      <Title level={4}></Title>
      <Paragraph>{prescriptionData.importance}</Paragraph>
      
      {/*  */}
      <Title level={4}></Title>
      <Paragraph>
        : {prescriptionData.prescription.where.join('')}<br/> {/*  */}
        : {prescriptionData.prescription.howLong} {/*  */}
      </Paragraph>
      
      {/*  */}
      <Title level={4}></Title>
      <Paragraph>
        : {prescriptionData.timingAndPrecautions.timing}<br/> {/*  */}
        : {prescriptionData.timingAndPrecautions.preparation}<br/> {/*  */}
        : {prescriptionData.timingAndPrecautions.reminders} {/*  */}
      </Paragraph>
      
      {/*  deficiencySymptoms  */}
      {prescriptionData.deficiencySymptoms && prescriptionData.deficiencySymptoms.length > 0 && (
        <>
          <Title level={4}></Title>
          {/*  */}
          <ul>{prescriptionData.deficiencySymptoms.map(symptom => <li key={symptom}>{symptom}</li>)}</ul>
        </>
      )}
      
      {/*  */}
      <Paragraph style={{ fontSize: '0.8em', color: '#777', marginTop: '1em' }}>
        
      </Paragraph>
    </Card>
  );
}

export default NaturalPrescriptionDetailCard;